@import '../../globalVariable';

.page {
  height: 100%;
  background: #f3f3f3;
}

.loginContainer {
  @include centerLayout;
  margin-top: 150px;
  padding: 30px 40px;
  box-sizing: border-box;
  box-shadow: 0 7px 20px 0 rgba(0,0,0,.06);
  background:#ffffff;

  .loginTitle {
    margin-bottom: 24px;
    padding-bottom: 18px;
    border-bottom: 1px solid #D8D8D8;
    font-size: 20px;
    font-weight: bold;
    line-height: 28px;
  }

  .accountInputWrapper {
    margin-bottom: 17px;
  }

  .passwordInputWrapper {
    margin-bottom: 30px;
  }

  .buttonWrapper {
    margin-bottom: 20px;

    .loginButton {
      background: #00B548;
    }

    .disabled {
      margin-top: 10px;
      background: #eaeaea;
      color: #616161;
      border: 1px solid #eaeaea;
    }
  }
}
