@import '../../globalVariable';

$iconSize: 16px;
$fullSize: 100%;
$height: 40px;
$borderWidth:1px;
$paddingTop:8px;
$paddingBottom:7px;
$inputInnerHeight:$height - $borderWidth*2 - $paddingTop -$paddingBottom;

.wrapper {
  position: relative;

  .inputWrapper {
    position: relative;
    height: $height;

    .input {
      width: $fullSize;
      height: $inputInnerHeight;
      padding:$paddingTop 0 $paddingBottom;
      border: $borderWidth solid #cacaca;
      border-radius: 6px;
      text-indent: 13px;
      line-height: $inputInnerHeight;

      &::placeholder {
        font-size: $middleFontSize;
        color: #848a8b;
      }
    }

    .tipIcon {
      position: absolute;
      top: 50%;
      right: 12px;
      width: $iconSize;
      height: $iconSize;
      transform: translateY(-50%);

      &.default, &.error {
        display: none;
      }

      &.warning {
        background: url('../../assets/checkmarker_yellow.png');
        background-size: contain;
      }

      &.correct {
        background: url('../../assets/checkmark.png');
        background-size: contain;
      }

      @include hide;
    }
  }

  .tipText {
    margin: 8px 13px;
    font-size: $smallFontSize;
    letter-spacing: -0.09px;
    line-height: 17px;

    &.error {
      color: $errorTipColor;
    }

    &.warning {
      color: #f6bd00;
    }

    &.default, &.correct {
      display: none;
    }
  }
}